body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


* {
  box-sizing: border-box;
}


.row { /* IE10 */
  display: flex; /* IE10 */
  flex-wrap: wrap;
  padding: 0 4px;
}

/* Create four equal columns that sits next to each other */
.column { /* IE10 */
  flex: 10% 1;
  max-width: 100%;
  padding: 0 4px;
}

.column img {
  margin: 15px;
  vertical-align: middle;
}

/* Responsive layout - makes a two column-layout instead of four columns */
@media screen and (max-width: 800px) {
  .column {
      flex: 50% 1;
      max-width: 50%;
  }
}

/* Responsive layout - makes the two columns stack on top of each other instead of next to each other */
@media screen and (max-width: 600px) {
  .column {
      flex: 100% 1;
      max-width: 100%;
  }
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: Arial;
}

.content{
  margin:0 auto;
  width: 98%;
}

.row { /* IE10 */
  display: flex-fluid; /* IE10 */
  flex-wrap: wrap;
  margin: 0 auto;
}

/* Create four equal columns that sits next to each other */
.column { /* IE10 */
  flex: 15% 1;
  max-width: 15%;
  background-color:#ecf0f1;
  margin: 10px;
  border-radius: 25px;
}

.column img {
  margin-top: 8px;
  vertical-align: middle;
  width: 100%;
  max-width: 250px;
  height: auto;
}

/* Responsive layout - makes a two column-layout instead of four columns */
@media screen and (max-width: 800px) {
  .column {
      flex: 50% 1;
      max-width: 50%;
  }
}

/* Responsive layout - makes the two columns stack on top of each other instead of next to each other */
@media screen and (max-width: 600px) {
  .column {
      flex: 100% 1;
      max-width: 100%;
  }
}


#popup {
  visibility: hidden;
  opacity: 0;
  margin-top: -300px;
}
#popup:target {
  visibility:visible;
  opacity: 1;
  background-color: rgba(0,0,0,0.8);
  position: fixed;
  top:0;
  left:0;
  right:0;
  bottom:0;
  margin:0;
  z-index: 999;
  transition:all 1s;
}
.popup-contenedor {
  position: relative;
  margin:7% auto;
  padding:30px 50px;
  background-color: #fafafa;
  color:#333;
  border-radius: 3px;
  width:40%;
  text-align: center;
}


a.popup-cerrar {
  position: absolute;
  top:3px;
  right:3px;
  background-color: #333;
  padding:7px 10px;
  font-size: 20px;
  text-decoration: none;
  line-height: 1;
  color:#fff;
}
